
$spinner-color: color($spinner-animation-color);
.spinner {
    margin: 50px auto;
    font-size: 10px;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    text-indent: -9999em;
    -webkit-animation: spinner 1.1s infinite ease;
    animation: spinner 1.1s infinite ease;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
  }
  .spinner-status{
    text-align: center;
  }


  @-webkit-keyframes spinner {
    0%,
