      box-shadow: 0em -2.6em 0em 0em $spinner-color, 1.8em -1.8em 0 0em rgba($spinner-color, 0.2), 2.5em 0em 0 0em rgba($spinner-color, 0.2), 1.75em 1.75em 0 0em rgba($spinner-color, 0.2), 0em 2.5em 0 0em rgba($spinner-color, 0.2), -1.8em 1.8em 0 0em rgba($spinner-color, 0.2), -2.6em 0em 0 0em rgba($spinner-color, 0.5), -1.8em -1.8em 0 0em rgba($spinner-color, 0.7);
